$buttonDetallesColor: #b1e957;
$insetShadow: #2c3a1644;

.logo-h4{
    animation-duration: 2900ms;
    animation-name: titleChange;
    animation-iteration-count: infinite;
}
.button-right-side > button{
    animation-duration: 18500ms;
    animation-name: buttonAnimation;
    animation-iteration-count: infinite;
}

@keyframes titleChange {
    from {
        color: white;
        text-shadow: -7px 3px 0px #00000026;
    }
    
    10% {
        color: white;
        text-shadow: -2px 8px 0px #00000026;
    }
    20% {
        color: #b1e957;
        text-shadow: -7px 3px 0px #ffffff;
    }
    30% {
        color: white;
        text-shadow: -9px 3px 0px #00000026;
    }
    40% {
        color: white;
        text-shadow: -5px 4px 0px #00000026;
    }
    50% {
        color: #ace355;
        text-shadow: 6px 2px 0px #ffffff;
    }
    68% {
        color: white;
        text-shadow: 10px 1px 0px #00000026;
    }
    70% {
        opacity: 0.7;
        color: #00000026;
        text-shadow: 11px 1px 0px #ec366d;
    } 
    75%{
        color: #00000026;
        text-shadow: 8px 1px 3px #ec366d;
    }
    80% {
        color: #ec366d;
        text-shadow: -7px 3px 2px black;
    }
    90% {
        color: white;
        text-shadow: -7px 3px 0px black;
    }
    100% {
        color: white;
        text-shadow: -7px 3px 0px black;
    }
}
@keyframes buttonAnimation {
    from {
        color: #b8ff46;
    }
    10% {
        color:  #b8ff4680;
    }
    20% {
        color: #b8ff46;
    }
    30% {
        color:  #b8ff4680;
    }
    40% {
        color: #b8ff46;
    }
    50% {
        color:  #b8ff4680;
    }
    60% {
        color: #b8ff46;
    }
    70% {
        color:  #b8ff4680;
    } 
    80%{
        color: #b8ff46;
    }
    90% {
        color:  #b8ff4680;
    }
    100% {
        color: #b8ff46;
    }
}
@keyframes buttonDetalles {
    0% {
        box-shadow: 0px 0px 14px 4px  $buttonDetallesColor;
    }
    5%{
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
    10% {
        box-shadow: 0px 0px 14px 4px $buttonDetallesColor;
    }
    20% {
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
    30% {
        box-shadow: 0px 0px 14px 4px $buttonDetallesColor;
    }
    40% {
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
    50% {
        box-shadow: 0px 0px 14px 4px $buttonDetallesColor;
    }
    60% {
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
    70% {
        box-shadow: 0px 0px 14px 4px $buttonDetallesColor;
    }
    80% {
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
    90% {
        box-shadow: 0px 0px 14px 4px $buttonDetallesColor;
    }
    100% {
        box-shadow: 0px 0px 4px 0px $buttonDetallesColor;
    }
}
@keyframes buttonBuy {
    0% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    5%{
        color: black;
        text-shadow: 0px 0px 5px black;
    }
    10% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    20% {
        color: black;
        text-shadow: 0px 0px 5px black;
        box-shadow: 0px 0px 10px white;
    }
    30% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    40% {
        color: black;
        text-shadow: 0px 0px 5px black;
    }
    50% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    60% {
        color: black;
        text-shadow: 0px 0px 5px black;
    }
    70% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    80% {
        color: black;
        text-shadow: 0px 0px 5px black;
    }
    90% {
        color: #2c3a1687;
        text-shadow: 0px 0px 2px black;
        box-shadow: 0px 0px 10px white;
    }
    100% {
        color: black;
        text-shadow: 0px 0px 5px black;
    }
}
@keyframes buttonBuyShadow {
    0% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    5%{
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px $insetShadow;
        border-radius: 1px solid $insetShadow;
    }
    10% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    20% {
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px $insetShadow;
        border-radius: 1px solid $insetShadow;
    }
    30% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    40% {
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px $insetShadow;
        border-radius: 1px solid $insetShadow;
    }
    50% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    60% {
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px $insetShadow;
        border-radius: 1px solid $insetShadow;
    }
    70% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    80% {
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px $insetShadow;
        border-radius: 1px solid $insetShadow;
    }
    90% {
        box-shadow: 0px 0px 14px 2px #2c3a16ba;
    }
    100% {
        box-shadow: 0px 0px 14px 2px $insetShadow, inset 0px 0px 14px 2px #2c3a1644;
        border-radius: 1px solid $insetShadow;
    }
}
